<template>
  <b-modal
    id="modal-xl"
    size="lg"
    hide-footer
    title="Ajouter une nouvelle compétence"
    centered
    ref="createModel"
  >
    <div style="padding: 15px">
      <div class="row mt-3">
        <div class="col col-md-4">Matière :</div>
        <div class="col col-md-6">
          <select
            style="font-size: 15px; font-weight: normal"
            @change="validate('matiere')"
            v-model="formData.matiere"
            class="form-select form-select-lg"
            aria-label=".form-select-lg example"
          >
            <option :value="null">Sélectionner une matière</option>
            <option
              v-for="(item, index) in matieres"
              :key="index"
              :value="item.id"
            >
              {{ item.label }}
            </option>
          </select>
          <span v-if="errors.matiere" class="err">
            La matière est obligatoire</span
          >
        </div>
      </div>

      <div class="row mt-3">
        <div class="col col-md-4">Affecter à cette compétence :</div>
        <div class="col col-md-6">
          <select
            style="font-size: 15px; font-weight: normal"
            v-model="formData.comp_mam"
            class="form-select form-select-lg"
            aria-label=".form-select-lg example"
          >
            <option :value="null">Aucune</option>
            <option
              v-for="(item, index) in competences"
              :key="index"
              :value="item.id"
            >
              {{ item.label }}
            </option>
          </select>
        </div>
      </div>

      <div class="row mt-3">
        <div class="col col-md-4">Titre de la Compétence :</div>
        <div class="col col-md-8">
          <b-form-textarea
            @keyup="validate('comp_title')"
            v-model="formData.comp_title"
            placeholder="Titre de la Compétence ..."
            rows="2"
            max-rows="6"
          ></b-form-textarea>

          <span v-if="errors.comp_title" class="err">
            Le titre de la Compétence est obligatoire</span
          >
        </div>
      </div>

      <div class="row mt-3">
        <div class="col col-md-4">Plus de détails :</div>
        <div class="col col-md-8">
          <b-form-textarea
            v-model="formData.comp_description"
            placeholder="Description de la compétence ..."
            rows="5"
            max-rows="6"
          ></b-form-textarea>
        </div>
      </div>

      <div class="row mt-3">
        <div class="col col-md-4">Masse horaire :</div>
        <div class="col col-md-4">
          <b-form-input
            type="number"
            @keyup="validate('mass_hour')"
            v-model="formData.mass_hour"
          >
          </b-form-input>
          <span v-if="errors.mass_hour" class="err">
            Masse horaire est obligatoire</span
          >
        </div>
      </div>

      <div class="row mt-3">
        <div class="col col-md-4">Programation Recommandée :</div>
        <div class="col col-md-6">
          <date-picker
            @change="validate('range_prog_recom')"
            range
            v-model="formData.range_prog_recom"
            value-type="YYYY-MM-DD"
            inline
          ></date-picker>
          <span v-if="errors.range_prog_recom" class="err">
            Période est obligatoire</span
          >
        </div>
      </div>
      <div class="row mt-5 center">
        <div class="col col-md-4">
          <b-button variant="primary" class="m-1" @click="addAndStay()"
            >Enregistrer et ajouter une nouvelle compétence
          </b-button>
        </div>
        <div class="col col-md-3">
          <b-button variant="primary" class="m-1" @click="add()"
            >Ajouter la compétence</b-button
          >
        </div>
        <div class="col col-md-3">
          <b-button variant="white" class="m-1" @click="closePopUp"
            >Annuler
          </b-button>
        </div>
      </div>
    </div>
  </b-modal>
</template>

<script>
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
import { listesCompetences, addCompetence } from "../apis/index";
export default {
  name: "CreateCompModel",
  props: ["refresh", "matieres", "niveau", "unite"],
  components: {
    DatePicker,
  },
  data: () => ({
    competences: [],
    formData: {
      niveau: null,
      matiere: null,
      comp_mam: null,
      comp_title: null,
      comp_description: null,
      mass_hour: null,
      range_prog_recom: null,
    },
    errors: {
      matiere: false,
      comp_title: false,
      mass_hour: false,
      range_prog_recom: false,
    },
  }),
  watch: {
    niveau() {
      this.listesCompetences();
    },
  },
  methods: {
    listesCompetences(niveau, unite) {
      if (niveau && unite)
        listesCompetences(niveau, unite).then((res) => {
          this.competences = res.data;
        });
    },
    add() {
      console.log(this.isValidated());
      if (!this.isValidated()) {
        return;
      }
      let formData = new FormData();
      this.formData.niveau = this.niveau;
      Object.keys(this.formData).forEach((key) => {
        if (this.formData[key]) formData.append(key, this.formData[key]);
      });
      addCompetence(formData).then(() => {
        this.$refs["createModel"].hide();
        this.listesCompetences(this.niveau, this.unite);
        this.clearData();
        this.refresh();
      });
    },
    clearData() {
      Object.keys(this.formData).forEach((key) => {
        this.formData[key] = null;
      });
      Object.keys(this.errors).forEach((key) => {
        this.errors[key] = false;
      });
    },
    isValidated() {
      let isvalid = true;
      for (let key in this.errors) {
        if (this.validate(key)) {
          isvalid = false;
        }
      }
      return isvalid;
    },
    addAndStay() {
      console.log(this.isValidated());
      if (!this.isValidated()) {
        return;
      }
      let formData = new FormData();
      this.formData.niveau = this.niveau;
      Object.keys(this.formData).forEach((key) => {
        formData.append(key, this.formData[key]);
      });
      addCompetence(formData).then(() => {
        this.listesCompetences(this.niveau, this.unite);
        this.clearData();
        this.refresh();
      });
    },
    validate(field) {
      if (!this.formData[field]) {
        return (this.errors[field] = true);
      } else {
        return (this.errors[field] = false);
      }
    },
    closePopUp() {
      this.clearData();
      this.$refs["createModel"].hide();
    },
  },
};
</script>

<style>
.err {
  color: red;
  margin-left: 10px;
  font-size: 12px;
}
</style>