<template>
  <div class="Expander">
    <div class="row m-0">
      <div class="col col-md-7 col-title">
        <div
          :style="{
            'margin-left': level * 20 + (!hasSub ? 30 : 0) + 'px',
          }"
        >
          <span
            class="Expender_opener"
            v-if="hasSub"
            @click="open = !open"
            v-html="open ? '-' : '+'"
          >
          </span>
          <slot name="header_title"></slot>
          <br />
          <span v-if="$slots.header_des">
            <span style="margin-left: 25px; float: left" v-if="hasSub"> </span>
            <p class="des">
              <slot name="header_des"></slot>
            </p>
          </span>
        </div>
      </div>
      <div class="col col-pad">
        <slot name="header_col1"></slot>
      </div>
      <div class="col col-pad"><slot name="header_col2"></slot></div>
      <div class="col col-pad"><slot name="header_col3"></slot></div>
      <div class="col col-md-2 col-pad"><slot name="header_col4"></slot></div>
    </div>
    <transition :name="animation">
      <div v-show="open">
        <expender
          v-for="(competence, index) in children"
          :children="competence.subCompetences"
          :hasSub="competence.subCompetences.length != 0"
          :key="index"
          :level="level + 1"
        >
          <template v-slot:header_title> {{ competence.titre }} </template>

          <template v-if="competence.description" v-slot:header_des>
            {{ competence.description }}
          </template>

          <template v-slot:header_col1>
            {{ competence.masse_horaire }}
          </template>

          <template v-slot:header_col2> {{ competence.res_peda }}</template>
          <template v-slot:header_col3>{{ competence.eva_prevu }} </template>
          <template v-slot:header_col4>
            <span>
              <b> {{ competence.date_debut }} </b>
              à
              <b> {{ competence.date_fin }} </b>
            </span>
          </template>
        </expender>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "Expender",
  props: {
    animation: {
      type: String,
      default: "rightToLeft",
    },
    hasSub: {
      type: Boolean,
      default: true,
    },
    children: {
      default: [],
    },
    level: {
      default: 0,
    },
  },
  data() {
    return {
      open: false,
    };
  },
};
</script>

