import Vue from 'vue';


export function competences(niveau, unite) {

    return Vue.axios.get(`/admin/competences/tree_competences/${niveau}/${unite}`);

}


export function listesCompetences(niveau, unite) {

    return Vue.axios.get(`/admin/competences/listes_competences/${niveau}/${unite}`);

}

export function listesNiveaux() {

    return Vue.axios.get('/admin/competences/listes_niveaux');

}

export function listesMatiere(unite) {

    return Vue.axios.get(`/admin/competences/listes_matieres/${unite}`);

}

export function listesUnites() {

    return Vue.axios.get('/admin/competences/listes_unites');

}

export function addCompetence(data) {

    return Vue.axios.post('/admin/competences/add', data);

}
