<template>
  <div id="app">
    <div class="row">
      <div class="col col-md-8 to-left">
        <h3>Matrice des compétences</h3>
      </div>
      <div class="col col-md-4 to-right row">
        <select v-model="unite" class="form-select m-1" style="width: 45%">
          <option v-for="(item, index) in unites" :key="index" :value="item.id">
            {{ item.label }}
          </option>
        </select>
        <select v-model="niveau" class="form-select m-1" style="width: 45%">
          <option
            v-for="(item, index) in niveaux"
            :key="index"
            :value="item.id"
          >
            {{ item.label }}
          </option>
        </select>
      </div>
    </div>
    <div class="row mt-5">
      <div class="col col-md-6"></div>
      <div class="col col-md-6 to-right">
        <div>
          <b-button variant="white" v-b-modal.modal-xl class="m-1 card-border"
            >Nouvelle compétence</b-button
          >
          <b-button variant="white" class="m-1 card-border"
            >Calendier des compétences</b-button
          >
        </div>
      </div>
    </div>
    <div class="row table_border m-0">
      <div class="row header_table m-0">
        <div class="col col-md-7 col-title center">compétences</div>
        <div class="col col-md-1 col-pad-header">Masse horaire</div>
        <div class="col col-md-1 col-pad-header">Ressources pédagogiques</div>
        <div class="col col-md-1 col-pad-header">Evaluations Prévues</div>
        <div class="col col-md-2 col-pad-header">Période recommandée</div>
      </div>

      <expender
        v-for="(competence, index) in competences"
        :key="index"
        :children="competence.subCompetences"
        :hasSub="competence.subCompetences.length != 0"
      >
        <template v-slot:header_title>
          {{ competence.titre }}
        </template>
        
        <template v-if="competence.description" v-slot:header_des>
          {{ competence.description }}
        </template>

        <template v-slot:header_col1>
          {{ competence.masse_horaire }}
        </template>

        <template v-slot:header_col2> {{ competence.res_peda }}</template>
        <template v-slot:header_col3>{{ competence.eva_prevu }} </template>
        <template v-slot:header_col4>
          <span>
            <b> {{ competence.date_debut }} </b> à
            <b> {{ competence.date_fin }} </b>
          </span></template
        >
      </expender>
    </div>
    <create-comp-model
      ref="createModel"
      :refresh="getCompetences"
      :matieres="matieres"
      :unite="unite"
      :niveau="niveau"
    />
  </div>
</template>

<script>
import Expender from "./components/Expender";
import CreateCompModel from "./components/CreateCompModel.vue";
import {
  competences,
  listesNiveaux,
  listesUnites,
  listesMatiere,
} from "./apis/index";
export default {
  name: "App",
  components: {
    Expender,
    CreateCompModel,
  },
  data: () => ({
    unite: null,
    niveau: null,
    unites: [],
    niveaux: [],
    matieres: [],
    competences: [],
  }),
  watch: {
    unite() {
      this.getMatieres();
      this.getCompetences();
      if (this.niveau && this.unite)
        this.$refs.createModel.listesCompetences(this.niveau, this.unite);
    },
    niveau() {
      this.getCompetences();
      if (this.niveau && this.unite)
        this.$refs.createModel.listesCompetences(this.niveau, this.unite);
    },
  },
  mounted() {
    this.axios.all([listesNiveaux(), listesUnites()]).then(
      this.axios.spread((listesNiveaux, listesUnites) => {
        this.niveaux = listesNiveaux.data;
        this.unites = listesUnites.data;
        this.unite = this.unites[0].id;
        this.niveau = this.niveaux[0].id;
        this.getCompetences();
      })
    );
  },
  methods: {
    getCompetences() {
      competences(this.niveau, this.unite).then((res) => {
        this.competences = res.data;
      });
    },
    getMatieres() {
      listesMatiere(this.unite).then((res) => {
        this.matieres = res.data;
      });
    },
  },
};
</script>

